          </select> -->
        </div>
        <!-- <p>Atasan (Default): </p>
              <v-select :options="units2" label="nama" v-model="vdata.atasan"></v-select> -->
        <br />

        <div class="md-form form-sm">
          <span class="typcn typcn-phone text-lg"></span>&nbsp;&nbsp;&nbsp;
          <label for="nohp">NO WA </label>
          <input
            type="text"
            id="nohp"
            name="nohp"
            class="form-control"
            v-model="vdata.nohp"
            required
          />
        </div>
        <div class="md-form form-sm">
          <span class="typcn typcn-image text-lg"></span>&nbsp;&nbsp;&nbsp;
          <label for="gambar">Gambar (optional)</label>
          <input type="file" id="filenya" accept="image/*" />
          <button
            type="button"
            @click="previewImage"
            class="btn btn-sm btn-danger"
          >
            Upload!
          </button>
          <hr class="style2" />
        </div>
        <!-- <p>Notifikasi dengan Email ? <span class="font-bold font-times"> 
          {{email ?'aktif':"tidak aktif"}}
          </span> 
          </p>
        <button type="button" class="btn btn-sm" @click="email=false;vdata.emails=false" :class="{'btn-secondary':!email}">OFF</button>
        <button type="button" class="btn btn-sm" @click="email=true;vdata.emails=true" :class="{'btn-primary':email}">ON</button> -->
        <div class="md-form form-sm" v-if="email">
          <span class="typcn typcn-mail text-lg"></span>&nbsp;&nbsp;&nbsp;
          <label for="email">Email</label>
          <input
            type="email"
            id="email"
            name="email"
            class="form-control"
            required
            v-model="vdata.emailnotif"
          />
        </div>
        <p class="text-red text-sm font-bold">
          Untuk atasan pada user ini minta set ke Admin !
        </p>
        <p class="text-red text-sm font-bold">
          Klik tombol bawah untuk set device notifikasi !
        </p>
        <button
          type="button"
          @click="addnotif()"
          style="font-size: 12px; background: grey"
          class="btn btn-sm btn-style9 ml-2"
        >
          <span class="typcn typcn-bell"></span>
        </button>
        <br />
        <br />

        <br />
        <button
          type="button"
          @click="$router.go(-1)"
          class="btn btn-md btn-dark"
