<script>
import axios from "axios";
import firebase from "firebase";
import imageCompression from "browser-image-compression";
let db = firebase.firestore();
import autophp from "@/plugins/autophp.js";
let sdb = new autophp();

export default {
  data() {
    return {
      vdata: {
        nama: "",
        unit: "",
      },
      units: [],
      units2: [],
      unit: "",
      imageData: null,
      picture: null,
      file: "",
      email: false,
      uploadValue: 0,
    };
  },
  beforeMount() {
    //doc adalah select *
    sdb
      .collection("tbuser")
      .doc(this.$store.state.users.id)
      .get("*")
      .then((res) => {
        console.log(res[0]);
        this.vdata.nip = res[0].nip;
        this.vdata.nama = res[0].nama;
        this.vdata.unit = res[0].unit;
        this.vdata.unit_kerja = res[0].unit_kerja;
        // this.vdata.emails=this.$store.state.users.emails
        // this.email=this.$store.state.users.emails
        // this.vdata.emailnotif =res[0].emailnotif;
        this.vdata.gambar = res[0].gambar ? res[0].gambar : "";
        this.vdata.nohp = res[0].nohp ? res[0].nohp : "";
      });
  },
  layout: "lembur",
  methods: {
    addnotif() {
      // !FCM
      document.addEventListener(
        "deviceready",
        function () {
          var push = PushNotification.init({
            android: {
              senderID: "494678481790", // setting project firebase
              sound: "true",
              vibrate: "true",
              clearNotifications: true, // clear notifications from shade on app start
              forceShow: true,
            },
            ios: {
              alert: "true",
              badge: "true",
              sound: "true",
            },
          });
          PushNotification.createChannel(
            () => {
              console.log("success");
            },
            () => {
              console.log("error");
            },
            {
              id: "test-channel",
              description: "My first test channel",
              importance: 5,
              vibration: [500, 1000, 500, 500],
              sound: "test", // test adalah test.wav nama file sound notification
            }
          );
          push.on("registration", function (data) {
            // ! AMBIL data registrationId untuk ditaruh sebagai payload to : ""
            // ! simpan data regitrationId kedatabase
            localStorage.setItem("notifid", data.registrationId);
            that.regis = data.registrationId;
            alert(that.regis);
            that.$forceUpdate();
            if (this.$store.state.users) {
              //idnya letak di vdata
              sdb
                .collection("tbuser")
                .doc()
                .set({
                  id: this.$store.state.users.id,
                  fcm: data.registrationId,
                })
                .then((res) => {
                  alert(
                    "BERHASIL SET DEVICE INI UNTUK MENDAPATKAN NOTIFIKASI!"
                  );
                });
            }
            // Simpan data.registrationId KE DATABASE DENGAN GANDENGAN ID USER untuk di tembak dengan payload {to : ""} firebase
          });
          push.on("notification", function (data) {});
          push.on("error", function (e) {
            console.log(e.message);
          });
        },
        false
      );
    },
    hapus() {
      let that = this;
      if (confirm("Apakah yakin menghapus akun ini ?")) {
        sdb
          .collection("tbuser")
          .doc(this.$store.state.users.id)
          .delete()
          .then((res) => {
            console.log(res);
          });
        alert("berhasil hapus akun");
        that.$store.dispatch("logout");
      }
    },
    next() {
      //idnya letak di vdata
      let that = this;
      sdb
        .collection("tbuser")
        .doc()
        .set({
          id: this.$store.state.users.id,
          ...this.vdata,
        })
        .then((res) => {
          console.log(res);
          alert("Simpan Berhasil");
          //doc adalah select *
          sdb
            .collection("tbuser")
            .doc(that.$store.state.users.id)
            .get("*")
            .then((res) => {
              // this.$store.state.users=res[0];
              console.log(res[0]);
              that.$store.commit("changeUsers", res[0]);
              // sdb.collection().ceklogin(this.$store).then(res=>{
              //             this.$router.push('/lembur')
              //           }).catch(err=>{
              //             this.$router.push('/login')
              //   })
            });

          // this.$store.dispatch('ceklogin',{true:true,false:true}).then(res=>{
          //   this.$router.push("/lembur");
          // })
        });
    },
    async previewImage() {
       let that = this;
      let el = document.querySelector("#filenya");
       const options = {
         maxSizeMB: 1,
        maxWidthOrHeight: 1920,
        useWebWorker: true
      }
      try {
        let file = el.files[0]; // tambahkan data set kosong file:""
        let fd = new FormData();
        const compressedFile = await imageCompression(file, options);
        fd.append("file", compressedFile);
        fd.append('data', `select * from tbuser`)//database setting di server
        fd.append('secret', `dsdxxoi4#$(*#sdsaaada@#`)//database setting di server
        axios.post('https://upload.egov.balangankab.go.id/upload1.php', fd
       ).then(res => {
            that.$forceUpdate();
            that.$nuxt.$emit("busy", false);
            that.$store.state.users.gambar =
              "https://upload.egov.balangankab.go.id" + res.data;
            that.vdata.gambar =
              "https://upload.egov.balangankab.go.id" + res.data;
            sdb
              .collection("tbuser")
              .doc()
              .set({
                id: that.$store.state.users.id,
                gambar:
                  "https://upload.egov.balangankab.go.id" + res.data,
                ...that.vdata,
              })
              .then((res) => {
                alert(
                  "Berhasil di upload ! klik simpan untuk menyimpan gambar"
                );
                that.$forceUpdate();
                sdb
                  .collection()
                  .ceklogin(that.$store)
                  .then((res) => {})
                  .catch((err) => {});
              });
        })

      } catch (error) {
        console.log(error);
      }
    },
    onUpload() {
      this.picture = null;
      const storageRef = firebase
        .storage()
        .ref(`${this.imageData.name}`)
        .put(this.imageData);
      storageRef.on(
        `state_changed`,
        (snapshot) => {
          this.uploadValue =
            (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        },
        (error) => {
          console.log(error.message);
        },
        () => {
          this.uploadValue = 100;
          storageRef.snapshot.ref.getDownloadURL().then((url) => {
            this.picture = url;
            console.log("url", url);
            this.vdata.gambar = url;
          });
        }
      );
    },
  },
  mounted() {
    // db.collection('users').where('bagian','==','deputy').get().then(res=>{
    //   let data = res.docs.map(e=>{
    //     return {
    //       uid:e.id,
    //       ...e.data()
    //     }
    //   })
    //   this.units2=data
    // })
    // db.collection("unit")
    //   .get()
    //   .then(res => {
    //     let data = res.docs.map(e => {
    //       return {
    //         id: e.id,
    //         ...e.data()
    //       };
    //     });
    //     data = _.orderBy(data, ["nama_unit"], ["asc"]);
    //     this.units = data;
    //     console.log(this.units)
    //   });
  },
};
</script>